import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';

import ReactPlayer from 'react-player';
import './styles.css'
import { setSentimentNumber } from '../../actions/sentimentActions';
import moment from 'moment';

function Turnip() {
  const time = useSelector(state => state.systemTime);
  const user = useSelector(state => state.participantId);
  const [mediaTime, setMediaTime] = useState();
  const [startTime, setStartTime] = useState();
  const [sentimentScore, setSentimentScore] = useState(50);
  const dispatch = useDispatch();


  let totalSeconds = mediaTime;
  let hours = Math.floor(totalSeconds / 3600);
  totalSeconds %= 3600;
  let minutes = Math.floor(totalSeconds/ 60);
  let seconds = Math.floor(totalSeconds % 60);

  // Convert the time to pretty stuff
  minutes = String(minutes).padStart(2, "0");
  hours = String(hours).padStart(2, "0");
  seconds = String(seconds).padStart(2, "0");
  const formattedTime = hours + ":" + minutes + ":" + seconds;

  console.log("Media Time Type: ", typeof(mediaTime))

  useEffect(() => {

    const interval = setInterval(() => {
      dispatch(setSentimentNumber(sentimentScore, user, time, formattedTime, startTime))
    }, 500);

    return () => clearInterval(interval)
  }, [dispatch, user, time, mediaTime, formattedTime, startTime])

  const sentimentScoreSetting = (e) => {
    e.preventDefault();
    setSentimentScore(parseInt(e.target.value))
  }

  const createTicks = () => {
    let ticks = [];

    for (let i = 0; i < 101; i += 10) {
      ticks.push(<span key={i} className="tick">{i}</span>)
    }
    return ticks
  }

  return (
    <div style={{ height: '95vh', display: 'flex', flexDirection: "column", justifyContent: 'center', alignItems: 'center'}}>
      <div style={{ height: '100%', width: '100%', position: 'relative', marginTop: '10px' }}>
        <div id="overlay"></div>
        <ReactPlayer 
          url="https://www.youtube.com/watch?v=y7e-GC6oGhg"
          controls={false}
          width="100%"
          height="100%"
          playing={true}
          onReady={() => setStartTime(Date())}
          onProgress={(e) => setMediaTime(e.playedSeconds)}
          config={{
            youtube: {
              playerVars: {
                controls: 0,
                disablekb: 1
              }
            },
            vimeo: {
              controls: false
            }
          }}
        />
      </div>
      <h1 style={{ paddingTop: '15px', paddingBottom: '15px' }}>
        How interested are you? &nbsp; { sentimentScore }
      </h1>
      <div style={{ width: '90%', display: 'flex', flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
          <i className="far fa-frown fa-3x" style={{ marginRight: '10px' }}></i>
          <p style={{ textAlign: 'center'}}>Not Interested</p>
        </div>
        <div style={{ width: '90%', display: 'flex', flexDirection: 'column', paddingRight: '10px', paddingLeft: '10px' }}>
          <input className="slider" type="range" min="0" max="100" step="1" value={sentimentScore}
            onChange={sentimentScoreSetting}
            list="steplist"
            autoFocus
          />
          <div className="ticks">
            {createTicks()}
          </div>
        </div>
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
          <i className="far fa-smile fa-3x" style={{ marginLeft: '10px' }}></i>
          <p style={{ textAlign: 'center' }}>Very Interested</p>
        </div>
      </div>
    </div>
  )
}

export default Turnip;